.GetInTouchWrapper {
    display: flex;
    color: #fff;
    flex-direction: column;
    padding: 0px 80px;
    margin: 80px 0px;
}
.GetInTouchWrapper h2 {
    text-align: center;
    font-size: 28px;
}
.GetInTouchWrapper p {
    text-align: center;
    font-size: 22px;
    padding: 0px 20px;
}
.GetInTouchHrLine {
    background-color: #fff;
    border: none;
    height: 2px;
    width: 100%;
    border-radius: 5px;
    margin: 20px 0px;
}
.mailIcon {
    text-align: center;
    font-size: 60px;
    margin: 30px 0px 20px;
}
.email {
    font-size: 22px;
    text-align: center;
    color: rgb(226, 152, 68);
    padding: 0px;
    margin: 0px;
}